@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
html, body, #app, #app>div {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
  background-color: white;
  background-image: url(/f1-races/static/media/background-compressed.0a8a0c0b.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.App {
  text-align: center;
  height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-item {
    padding-left: 2rem;
}

@media only screen and (max-width : 768px) {
    .nav-item {
        padding-left: 0px;
    }
}
.home-image {
    max-width: 50%;
    margin-top: 3em;
}

.next-race {
    width: 100%;
    background: #f8f9fa;
    margin-top: 2em;
    padding: 5%;
    font-size: 2rem;
    font-family: 'Roboto', sans-serif;
    
}

@media only screen and (max-width: 600px) {
    .home-image {
        max-width: 90%;
    }
  }
html, body {
    height: 100%;
}

.component {
    height: calc(100% - 56px);
}

.table-container {
    overflow: auto;
    max-width: calc(100%);
    height: calc(100%);
    background: #f8f9fa;
    box-sizing: content-box;
    -webkit-overflow-scrolling: touch;
    margin-left: auto;
    margin-right: auto;
}

.custom-table {
    padding: 0px;
    -webkit-overflow-scrolling: touch;
}

.table-header {
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(212,0,0,1)), color-stop(100%, rgba(255,38,0,1)));
    background: -webkit-linear-gradient(45deg, rgba(212,0,0,1) 0%, rgba(255,38,0,1) 100%);
    background: linear-gradient(45deg, rgba(212,0,0,1) 0%, rgba(255,38,0,1) 100%);
    background-attachment:fixed;
    color: #f8f9fa;
}
